.mainVisualization {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: black;
}

.toolBar {
    display: grid;
    grid-gap: 0px 0px;
    grid-template-columns: auto auto auto auto auto auto auto auto;
    position: fixed;
    top: 90%;
    /*left: 30%;*/
    width: 100%;
    height: 12%;
    background-color: grey;
}

.starsList {
    height: .2%;
    overflow: hidden;
    overflow-y: scroll;
    position: relative;
}

.bookmarkList {
    display: grid;
    grid-gap: 0px 0px;
    grid-template-columns: auto auto auto auto auto auto auto auto;
    position: fixed;
    margin-left: 20px;
    top: 5%;
    width: 50%;
    height: 8%;
    color: white;
    background-color: transparent;
}

.legend {
    display: grid;
    grid-gap: 0px 0px;
    grid-template-columns: auto auto auto auto auto auto auto auto;
    position: fixed;
    top: 0;
    left: 50%;
    width: 50%;
    height: 8%;
    background-color: black;
}

.legend-2 {
    display: grid;
    grid-gap: 0px 0px;
    grid-template-columns: auto auto;
    position: fixed;
    top: 8%;
    left: 86%;
    width: 14%;
    height: 8%;
    background-color: black;
}

.grid-item-r {
    background-color: #F12F26;
    padding: 10px;
    font-size: 18px;
    text-align: center;
}
.grid-item-o {
    background-color: #F77526;
    padding: 10px;
    font-size: 18px;
    text-align: center;
}
.grid-item-y {
    background-color: #FAFF52;
    padding: 10px;
    font-size: 18px;
    text-align: center;
}
.grid-item-yw {
    background-color: #F5FAB7;
    padding: 10px;
    font-size: 18px;
    text-align: center;
}
.grid-item-w {
    background-color: #FFFFFF;
    padding: 10px;
    font-size: 18px;
    text-align: center;
}
.grid-item-bw {
    background-color: #C2E9FC;
    padding: 10px;
    font-size: 18px;
    text-align: center;
}
.grid-item-b {
    background-color: #289DFF;
    padding: 10px;
    font-size: 18px;
    text-align: center;
}
.grid-item {
    background-color: rgba(255, 255, 255, 0.8);
    padding: 9px;
    font-size: 14.5px;
    text-align: center;
}

.modal {
    position: fixed;
    text-align: right;
    left: 50%;
    top: 35%;
    width: 50% ;
    transform: translate(-50%, -35%);
    height: 75%;
    z-index: 9999;
}

.modalHeader {
    text-align: center;

}

.modalText {
    text-align: left;
    font-size: 20px;
}

.modalCredit {
    text-align: Center;
    font-size: 20px;
    padding-top: 0%;
}

.about {
    left: 65%;
}

.starDescription {
    transform: translate3d(25%, -40%, 0);
    text-align: left;
    /*background: #679698;*/
    background: #986967;
    position: fixed;
    color: black;
    width: 60vh;
    font-size: 30px;
    padding: 10px 15px;
    border-radius: 30px;
    /*border: solid #26B2D9;*/
    border: solid #D94D26;

}

.focusedDescription {
    transform: translate3d(25%, -60%, 0);
    text-align: left;
    background: #986967;
    color: black;
    width: 60vh;
    font-size: 30px;
    padding: 10px 15px;
    border-radius: 30px;
    border: solid #D94D26;
}

.miniMap {
    position: absolute;
    top: 90%;
    right: 0;
    width: 10%;
    height: 10%;
    background-color: white;
}

.tableStyle {
    background-color: grey;
    opacity: .7;
    color: black;
    width: 40vh;
    padding-left: 10px;
    padding-right: 1px;
    margin-top: 6px;
    margin-left: -20px;
    padding-bottom: 5px;
}
